body {
  font-family: sans-serif;
}

button {
  margin: 50px auto 0;
  display: block;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

button.hidden {
  display: none;
}

input, button {
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

input[type="submit"], button {
  color: #fff;
  cursor: pointer;
  background-color: rgba(45, 45, 233, .73);
  border: none;
}

#articlesWrapper {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.article-card {
  width: 30%;
  text-align: center;
  background-color: #c1c1c1;
  border-radius: 15px;
  padding: 20px;
}

.article-img {
  width: 100%;
  border-radius: 15px;
}

.article-link {
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(45, 45, 233, .73);
  border-radius: 15px;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
}

